import React from "react";

const Apiproduct = () => {
    fetch("https://dummyjson.com/products")
        .then((result) => result.json())
        .then((res) => {
            const data = res.products;
            let list = "";
            data.forEach((products) => {
                list += <p>${products.id}</p>;
               
            });
            console.log(data[0])
            document.querySelector('#content11').innerHTML = list;
            
        });

    

    return (
        <div className="container11">
            <div id="wrapper11">
                <div >
                    <p>Images</p>
                    <p id="content11">id</p>
                </div>
            </div>
        </div>
    );
};

export default Apiproduct;
